// Sign Up Form On Landing Page

import React, { useState, useEffect, useContext } from 'react'
import { Context } from "../../../Context.js";
import { Amplify, Auth } from 'aws-amplify';
import useAuth from '../../useAuth'
import { useNavigate } from "react-router-dom";


//import { Formik, Field } from 'formik'
//import validationSchema from './validationInquirySchema'
//import handleClick from '../../../handleClick.js'

const SignUpForm = () => {
    
    
    const { SignIn, SignUp } = useContext(Context);
    const [ isShowSignUp, setIsShowSignUp ] = SignUp;

    // Local states in SignUpForm Component
    const [errorSignUp, setErrorSignUp] = useState('')
    const [isConfirmCodeHidden, setIsConfirmCodeHidden] = useState(true);
    const [prompt, setPrompt] = useState('')

    const navigate = useNavigate();

    
    const [inputFields, setInputFields] = useState([
        {name: '', password: '', email: '', phone: '', confirmcode: ''}
    ])

    const handleFormChange = (index, event) => {
        let data = [...inputFields];
        data[index][event.target.name] = event.target.value;
        setInputFields(data);
        
    }

    
    async function signUp(username, password, email, phone) {
        
      try {
        const { user } = await Auth.signUp({
          username,
          password,
          attributes: {
            email,          // optional
//            phone,   // optional - E.164 number convention
            // other custom attributes
          },
          autoSignIn: { // optional - enables auto sign in after user is confirmed
            enabled: true,
          }
        });
        
        setIsConfirmCodeHidden(false);
        setIsShowSignUp(true);
        setErrorSignUp('');
        setPrompt('Check verfication code in email/phone');

        console.log(user);
      } catch (err) {
        console.log('Error signing up:', err);
        setErrorSignUp( <font color="red"> {err.message} </font> );
        setIsShowSignUp(true)
      }
    }


    async function confirmSignUp(username, code) {
      try {
        await Auth.confirmSignUp(username, code);
      } catch (err) {
        setErrorSignUp( <font color="red"> {err.message} </font> );
        console.log('error confirming sign up', err);
      }
    }
    
    
    const { currentUser } = useAuth();

    
    // On sign up submit, redirect to dashboard
    const handleSubmit = (e) => {
        
      console.log("Form Submitted");
    
      e.preventDefault();
      if (currentUser  !== null) {
//        localStorage.setItem("authenticated", true);
        navigate("/dashboard");
      }
    };
    
    
  return (
          

          
          <div className="login-form">
            <div className="form-box solid">
          <form onSubmit={handleSubmit}>
          
          
          {inputFields.map((input, index) => {
             return (<div className='items-center py-2 border-teal-500 sm:mx-24' key={index}>
                     
                 <div className="flex text-teal-200">   Email:
                      <input
                      className='w-full px-2 py-1 my-1 mr-3 text-gray-200 placeholder-teal-200 bg-transparent border-b border-b-2 border-teal-500 appearance-none sm:my-0 sm:leading-tight sm:border-b focus:outline-none'
                        type="email"
                        name='email'
                        value={input.email}
                        onChange={event => handleFormChange(index, event)}

                      />
                  </div>

                  <br></br>


                <div className="flex text-teal-200">Password:
                    <input
                     className='w-full px-2 py-1 my-1 mr-3 text-gray-200 placeholder-teal-200 bg-transparent border-b border-b-2 border-teal-500 appearance-none sm:my-0 sm:leading-tight sm:border-b focus:outline-none'
                      type="password"
                      name='password'
                      value={input.password}
                      onChange={event => handleFormChange(index, event)}

                    />
                </div>

                 <br></br>

                     
                    {/*<div class="flex text-teal-200">Name: */}
                    {/*    <input className='w-full px-2 py-1 my-1 mr-3 text-gray-200 placeholder-teal-200 */} {/*bg-transparent border-b border-b-2 border-teal-500 appearance-none sm:my-0 */} {/*sm:leading-tight sm:border-b focus:outline-none' */}
                       {/*    type="text" */}
                       {/*    name='name' */}
                       {/*    value={input.name} */}
                       {/*    onChange={event => handleFormChange(index, event)} */}
                       {/*  /> */}
                       {/*</div> */}
                       {/*<br></br> */}

                {/* <input */}
                {/*    type="phone"*/}
                {/*    name='phone'*/}
                {/*   placeholder='Phone'*/}
                {/*  value={input.phone}*/}
                {/*  onChange={event => handleFormChange(index, event)}*/}
                {/*    /> */}
                {/* <br></br> */}
                     
                 <input
                 className='w-full px-2 py-1 my-1 mr-3 text-gray-200 placeholder-teal-200 bg-transparent border-b border-b-2 border-teal-500 appearance-none sm:my-0 sm:leading-tight sm:border-b focus:outline-none'
                   type="confirmcode"
                   name='confirmcode'
                   placeholder='Confirm Code'
                   value={input.confirmcode}
                   onChange={event => handleFormChange(index, event)}
                   hidden={ isConfirmCodeHidden }

                 />

                
                 <br></br>



              </div>
            )
          })}
          
          
          
          
          <div className='px-4 py-3 bg-gray-50 sm:px-6'>
  
           {/* Submit Sign Up*/}
          
            <div>{prompt}</div>
          
            <button type='button' className='px-8 py-1 my-4 text-sm text-white bg-teal-500 border-4 border-teal-500 rounded sm:my-0 hover:bg-teal-700 hover:border-teal-700' onClick={() => {
                
                if (isConfirmCodeHidden) { {/* inputFields[0].name = inputFields[0].email*/}
                    signUp(inputFields[0].email, inputFields[0].password, inputFields[0].email, inputFields[0].phone);
                }
                else { {/* inputFields[0].name = inputFields[0].email*/}
                    confirmSignUp(inputFields[0].email, inputFields[0].confirmcode);
                }
                
                }}>
               {isConfirmCodeHidden ? "Sign Up" : "Confirm" }
            </button>
  
         {/* https://stackoverflow.com/questions/38521055/react-js-append-div-next-to-input-if-error  */}
          <div>{errorSignUp}</div>

         
  

          </div>
  
      </form>
    </div>
  </div>
          
          
          
  )
}

export { SignUpForm }
